import store from '@/store'

const isProd = process.env.NODE_ENV === 'production'

const getEntry = ({ entry, base, server } = {}) => {
  if (process.env.NODE_ENV !== 'production') {
    return entry
  }
  // preview mode
  if (/localhost:5000/.test(location.host)) {
    return [server.dev, base, /\/$/.test(base) ? '' : '/'].join('')
  }
  const match = location.host.match(/(dev|qa1?)-/)
  return match
    ? [server[match[1]] || '', base, /\/$/.test(base) ? '' : '/'].join('')
    : [server.prod || '', base, /\/$/.test(base) ? '' : '/'].join('')
}

const closeNavTab = (route) => {
  store.dispatch('removeTabs', route)
}

const appConfig = [
  {
    name: 'sub1',
    entry: '//localhost:9000/',
    container: '#scm-purchase',
    base: '/sub1',
    server: {
      dev: '//localhost:5000',
      qa: '',
      prod: '//tomieric.github.io/qb-mf'
    }
  },
  {
    name: 'sub2',
    entry: '//localhost:9200/',
    container: '#scm-finance',
    base: '/sub2',
    server: {
      dev: '//localhost:5000',
      prod: '//tomieric.github.io/qb-mf'
      // dev: 'https://dev-scm.ysha.com/',
      // qa: 'https://qa-scm.ysha.com/',
      // prod: 'https://scm.ysha.com/'
    }
  }
]

export default appConfig.map(app => ({
  ...app,
  entry: getEntry(app),
  activeRule: isProd ? `/qb-mf/#${app.base}` : `#${app.base}`,
  props: {
    routeBase: `${app.base}`,
    store,
    closeNavTab
  }
}))
