import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

const SET_PAGE_LOADING_STATE = 'SET_PAGE_LOADING_STATE'
const SET_NAV_TABS = 'SET_NAV_TABS'
const ADD_NAV_TABS = 'ADD_NAV_TABS'
const SET_ACTIVE_TAB_NAV = 'SET_ACTIVE_TAB_NAV'
const REMOVE_NAV_TABS = 'REMOVE_NAV_TABS'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pageLoading: false,
    navTabs: [],
    activeTabNav: '/'
  },
  mutations: {
    [SET_PAGE_LOADING_STATE] (state, loading) {
      state.pageLoading = loading
    },
    [SET_NAV_TABS] (state, data) {
      state.navTabs = data
    },
    [ADD_NAV_TABS] (state, item) {
      if (item && item.id === '/') return
      if (
        !(state.navTabs || []).some(t => t.id === item.id || `${t.id}/` === item.id)
      ) state.navTabs.push(item)
    },
    [SET_ACTIVE_TAB_NAV] (state, { id }) {
      state.activeTabNav = id
    },
    [REMOVE_NAV_TABS] (state, { id, to }) {
      const index = state.navTabs.findIndex(nav => nav.id === id)
      state.navTabs.splice(index, 1)

      // 全部关闭
      if (!state.navTabs.length) {
        state.activeTabNav = '/'
        return
      }

      // 子路由跳转
      if (to && to.path) {
        state.activeTabNav = to.path
        return
      }

      state.activeTabNav = index === 0
        ? state.navTabs[0].id
        : state.navTabs[index - 1].id
    }
  },
  actions: {
    setLoading ({ commit }, loading) {
      commit(SET_PAGE_LOADING_STATE, loading)
    },
    setNavTabs ({ commit }, data) {
      commit(SET_NAV_TABS, data)
    },
    removeTabs ({ commit }, data) {
      commit(REMOVE_NAV_TABS, data)
    },
    addNavTabs ({ commit }, item) {
      commit(ADD_NAV_TABS, item)
      commit(SET_ACTIVE_TAB_NAV, item)
    },
    setActiveTabNav ({ commit }, item) {
      commit(SET_ACTIVE_TAB_NAV, item)
    }
  },
  modules: {
  },
  plugins: [
    createPersistedState()
  ]
})
