<template>
  <div id="portal">
    <Layout v-loading="pageLoading">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>

      <!-- 子应用容器  -->
      <div
        v-for="app in subapps"
        :key="app.name"
        :id="app.container.slice(1)"
        class="router-view"
        v-show="$route.path.startsWith(getActiveRule(app))"
      >
      </div>
    </Layout>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { loadMicroApp } from 'qiankun'
import subappConfig from './app.config'

export default {
  data () {
    return {
      subapps: subappConfig.map(app => ({
        name: app.name,
        container: app.container,
        activeRule: app.activeRule
      }))
    }
  },

  computed: {
    ...mapState({
      pageLoading: state => state.pageLoading
    })
  },

  watch: {
    $route (route) {
      this.loadMicroApp(route)
    }
  },

  created () {
    // 子应用加载缓存
    this.cachedApp = {}
  },

  methods: {
    getActiveRule (app) {
      return process.env.NODE_ENV === 'production'
        ? app.activeRule.replace(/\/qb-mf\/#/, '')
        : app.activeRule.slice(1)
    },

    loadMicroApp (route) {
      const currentApp = subappConfig.find(app => route.path.startsWith(this.getActiveRule(app)))
      if (currentApp) {
        const cacheApp = this.cachedApp[currentApp.name]
        // console.log('----------->', cacheApp)
        if (!cacheApp) {
          this.$store.dispatch('setLoading', true)
          loadMicroApp(currentApp, {
            singular: false
          },
          {
            afterMount: () => this.$store.dispatch('setLoading', false)
          })
          this.cachedApp[currentApp.name] = {
            app: currentApp
            // ,
            // childrenRoute: [route.path]
          }
        }
        // else {
        //   if (!cacheApp.childrenRoute.some(path => path === route.path)) {
        //     cacheApp.childrenRoute.push(route.path)
        //     this.$store.dispatch('addNavTabs', { id: route.path })
        //   }
        // }
        // this.$store.dispatch('setLoading', false)
      }
    }
  }
}
</script>

<style lang="less">
#portal {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: hidden;
}
.router-view {
  width: 100%;
  height: 100%;

  > div {
    width: 100%;
    height: 100%;

    > div {
      width: 100%;
      height: 100%;
    }
  }
}
</style>
