<template>
  <qb-page-tabs
    :home="home"
    :tabs="tabs"
    :active-id="activeTabNav"
    :is-show-close-bar="true"
    @open-item="onClickTab"
    @close-item="onCloseTab"
    @close-other-item="onCloseOtherItem"
    @close-all-item="onCloseAllItem">
  </qb-page-tabs>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      home: {
        id: '/'
      },
      tabs: []
    }
  },

  computed: {
    ...mapState({
      navTabsCache: state => state.navTabs,
      activeTabNav: state => state.activeTabNav
    })
  },

  watch: {
    navTabsCache: {
      handler (tabs) {
        this.tabs = tabs
        if (!tabs.length) {
          this.$router.push({ path: '/' })
        }
      },
      immediate: true
    },

    activeTabNav (path) {
      if (this.$route.path !== path) {
        this.$router.push({ path }).catch(e => {})
      }
    }
  },

  methods: {
    onClickTab (tab) {
      this.$store.dispatch('setActiveTabNav', tab)
      this.$router.push({ path: tab.id })
    },

    onCloseTab (tab) {
      this.$store.dispatch('removeTabs', tab)
    },

    onCloseAllItem () {
      this.tabs = []
      this.$store.dispatch('setNavTabs', [])
      this.$store.dispatch('setActiveTabNav', this.home)
    },

    onCloseOtherItem (tab) {
      this.tabs = this.tabs.filter(
        t => t.id === tab.id
      )
      this.$store.dispatch('setNavTabs', this.tabs)
    }
  }
}
</script>
