<template>
  <div class="q-nav">
    <qb-menu-container
      :menu-list="menuList"
      @click-item="onClickMenuItem"
    ></qb-menu-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      menuList: [
        {
          menuName: '采购',
          icon: 'iconHomebeifen16',
          children: [
            {
              menuName: '采购列表',
              url: '/sub1'
            }
          ]
        },
        {
          menuName: '销售',
          icon: 'iconHomebeifen16',
          children: [
            {
              menuName: '销售列表',
              url: '/sub2'
            }
          ]
        }
      ]
    }
  },

  methods: {
    onClickMenuItem ({ data }) {
      this.$router.push({ path: data.url })
      this.$store.dispatch('addNavTabs', {
        id: data.url,
        name: data.menuName
      })
    },

    bindCurrent () {
      const path = window.location.pathname
      this.current = path
    }
  }
}
</script>

<style lang="less">
.q-nav {
  height: 100%;
  overflow: hidden;

  &:hover {
    overflow-y: auto;
  }

  .qb-menu-container {
    min-height: 100%;
    height: 0;

    .qb-menu-container_show {
      min-height: 100%;
    }
  }
}
</style>
